@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono&family=Sora&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input,textarea,button,select,a{-webkit-tap-highlight-color:rgba(0,0,0,0)}#container{font-family:'Sora', sans-serif;overflow-y:hidden;background-color:#1A1A1D;color:white;height:100vh;display:flex;justify-content:center;align-items:center}#container *{font-size:1em}#container #bank-display{border-radius:10%;border:solid white;background-color:black;color:white;padding:5%;margin:3%}#container #sound-display{border-radius:10%;border:solid white;background-color:black;color:white;padding:5%;margin:3%}#container #drum-machine{width:90%;height:100%;max-width:30rem;display:flex;flex-direction:column;justify-content:center;align-items:center}#container #drumpad-container{width:90%;padding-left:1%;max-height:50%}#container #drumpad-container:after{content:'';display:block;padding-top:100%}#container #drumpad-container .drum-pad{font-family:'Sora', sans-serif;font-size:1em;margin:1%;width:31%;height:31%;cursor:pointer;border-radius:5%;border:solid #C3073F;background-color:#4E4E50;color:white}#container #drumpad-container .drum-pad:active{transform:translate(2px, 2px);box-shadow:0 0;background-color:#ffffff5a}#container #drumpad-container .drum-pad-clicked{font-family:'Sora', sans-serif;font-size:1em;margin:1%;width:31%;height:31%}#container #switch-kit-btn{padding:2%;font-family:'Sora', sans-serif;font-size:1em;cursor:pointer;border-radius:5%;border:solid #C3073F;background-color:#4E4E50;color:white}#container #switch-kit-btn:active{transform:translate(2px, 2px);box-shadow:0 0;background-color:#ffffff5a}#container #slider-container{margin-top:3%}#container #slider-container #slider{cursor:pointer}#container #label{border-radius:10%;border:solid white;background-color:black;color:white;margin-top:1%;padding:2%}

